.about {
  position: relative;
  top: 0;
  height: 100vh;
  width: 100%;
  background-color: #fff;
  overflow: hidden;
}

.header {
  padding-top: 40px;
  text-align: center;
  font-size: 3rem;
  font-weight: 700;
  color: var(--text-color);
}

.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--text-color);
  z-index: 1;
}

.info {
  width: 60%;
}

.history {
  margin-bottom: 20px;
}

.history h3 {
  font-size: 2rem;
}

.history p {
  font-size: 1.2rem;
}

.skills {
  margin-bottom: 20px;
}

.skills h4 {
  font-size: 1.5rem;
}

.svg {
  height: 50px;
  margin-right: 8px;
}

.next h4 {
  font-size: 1.5rem;
}

.contact {
  display: flex;
  flex-direction: column;
}

.contact svg {
  margin: 10px 0;
  font-size: 50px;
  cursor: pointer;
}

a {
  all: unset;
}

.lDec {
  width: 220px;
  height: 440px;
  border-radius: 100px 500px 150px 80px;
  background-color: #afffb7;
  position: absolute;
  top: 20px;
  left: -100px;
  transform: rotate(35deg);
}

.mDec {
  width: 800px;
  height: 510px;
  background-color: #daffff;
  position: absolute;
  top: 50%;
  left: 53%;
  transform: translate(-50%, -50%) rotate(-10deg);
  border-radius: 220px 130px 500px 140px;
}

.rDec {
  width: 340px;
  height: 330px;
  border-radius: 0 400px 200px 0;
  background-color: #ff8282;
  position: absolute;
  bottom: -100px;
  right: -140px;
  transform: rotate(-110deg);
}

.rDec::before {
  content: '';
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #ff8282;
  position: absolute;
  right: -30px;
  bottom: 180px;
}

.rDec::after {
  content: '';
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #ff8282;
  position: absolute;
  right: 60px;
  bottom: 380px;
}

footer {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  color: var(--border-color);
}
